
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import Vue from 'vue'
export default {
props:{

},
name: 'pre-menu',
components:{
Cell,
Group,
CellBox
},
data () {
return {
showContent001: false,
menuOpts:[
{
text:"主页",
id:0,
value:"homePage",
level:1,
hasChild:false,
selected:false
},
{
text:"健康状况",
id:1,
value:"health",
level:1,
hasChild:false,
selected:false
},
{
text:"排班情况",
id:2,
value:"plan",
level:1,
hasChild:false,
selected:false
},
{
text:"重要信息",
id:3,
          value:"importInfo",
level:1,
hasChild:false,
selected:false
},
{
text:"航路数据",
id:4,
value:"airway",
level:1,
hasChild:false,
selected:false
},
{
text:"机场信息",
id:5,
value:"airport",
level:1,
hasChild:false,
selected:false
}
]
}
},
computed:{

},
methods:{
clickTest(event){
console.log(event.currentTarget.dataset.id);
},
addSelectBack(event){
let currentID=event.currentTarget.dataset.id;
console.log(currentID);
/*this.menuOpts[event.currentTarget.dataset.id].*/
this.menuOpts.map((val)=>{
if(val.hasChild==true){
val.child.map((childVal)=>{
childVal.selected=false;
if(childVal.id==currentID){
Vue.set(childVal,'selected',true)
}
return val
});

}else{
val.selected=false;
if(val.id==currentID){
Vue.set(val,'selected',true)
}
return val;
}
});
this.$emit('menuClick');
}
}
}



