
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {

  props:{

  },
  name: 'health',
  components:{
    Group,
    XSwitch
  },
  data () {
    return {
      value1:1,
      illAllCheckStatus:false,
      illUl:[
        {
          text:'体温、脉搏超过正常（正常体温36-37℃ 正常脉搏60-100次/分）',
          isOk:false
        },
        {
          text:'在安静状态下，收缩压超过19.3Kpa（145mmhg）舒张压超过12kpa（96mmhg):高血压病I期有自觉症状',
          isOk:false
        },
        {
          text:'心慌心悸与胸闷气短、左胸疼痛不适、下肢水肿与口唇紫绀等',
          isOk:false
        }
      ]

    }
  },
  methods:{
    illAllCheck(value){
      this.illUl.map((valueObj)=>{
        valueObj.isOk=value;
      });
    }

  }
}



